@import "shared.scss";

.roundButton {
  font: inherit;
  width: 150px;
  height: 40px;
  border-radius: 30px;
  color:$DARK-GREY;
  font-weight: bold;
  background: white;
  box-shadow: none;
  font-size:15px;
  border: none;
  -webkit-box-shadow: 2px 10px 34px -15px rgba(0,0,0,0.75);
  -moz-box-shadow: 2px 10px 34px -15px rgba(0,0,0,0.75);
  box-shadow: 2px 10px 34px -15px rgba(0,0,0,0.75);
  &:hover{
    cursor: pointer;
  }
}